import React, { useState } from 'react';
import styles from './AddBlock.module.css';
import { Paper, TextField, IconButton } from '@material-ui/core';
import AddCircleIcon from '@material-ui/icons/AddCircle';

const AddBlock = (props) => {
    const [title, setTitle] = useState('');
    const [error, setError] = useState('');
    const addBlockItem = (e) => {
        e.preventDefault()
        if (!title) setError('ERROR')
        else {
            props.addBlock(title)
            setTitle('')
        }
    }
    const changeTitle = (e) => {
        setError('')
        setTitle(e.currentTarget.value)
    }
    return (
        <Paper style={{ padding: '4px 4px 4px 12px', margin: '8px' }}>
            <form className={styles.form} noValidate autoComplete="off" onBlur={(e) => setError('')} >
                <TextField id="outlined-basic" label={props.placeholder || ''} variant="outlined" fullWidth size="small" onChange={(e) => changeTitle(e)} value={title} error={error!==''} />
                <IconButton color="primary" component="span" onClick={addBlockItem}><AddCircleIcon fontSize="large" /></IconButton>
            </form>
        </Paper>
    )
}
export default AddBlock; 
