import React from 'react';
import styles from './ProfileInfo.module.css';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';
import CardContent from '@material-ui/core/CardContent';
import ClearSharpIcon from '@material-ui/icons/ClearSharp';


const ProfileInfo = (props) => {
  const { username, name, email, phone, address } = props;
  const useStyles = makeStyles(theme => ({
    root: {
      minWidth: 275,
      backgroundColor: theme.palette.yellowColor
    },
    pos: {
      fontSize: 14,
    },
  }));
  const classes = useStyles();

  return (

    <div className={styles.profileInfo}>
      <Card className={classes.root}>
        {/* <span className={styles.close} onClick={props.closeProfile}>Close</span> */}
        <div className={styles.clearIcon}>
                <ClearSharpIcon onClick={props.closeProfile}/>
        </div>
        <CardContent>
          <Typography variant="h6" component="h6" gutterBottom>{username}</Typography>
          <Typography className={classes.pos} color="textSecondary" gutterBottom><b>Name: </b>{name}</Typography>
          <Typography className={classes.pos} color="textSecondary" gutterBottom><b>Email: </b>{email}</Typography>
          <Typography className={classes.pos} color="textSecondary" gutterBottom><b>Address: </b>{address.street}, {address.city}, {address.zipcode}</Typography>

        </CardContent>
      </Card>
    </div>
  )
}

export default ProfileInfo
