import { todosApi, tasksApi } from '../../api/todosApi'
const uuidv4 = require('uuid/v4')

const SET_TODOLISTS = 'todosReducer/SET_USER_TODOLISTS';
const ADD_TODO = 'todosReducer/ADD_TODO';
const DEL_TODO = 'todosReducer/DEL_TODO';
const IS_LOADING = 'todosReducer/SET_ISLOADING'

const initialState = {
  todosList: [],
  isLoading: true
}


const todosReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_TODOLISTS:
      return { ...state, todosList: action.todosList }
    case ADD_TODO:
      let newTodo = {
        title: action.title
      }
      return { ...state, todoLists: [...state.todosList, newTodo] }
    case DEL_TODO:
      return { ...state, todoLists: action.todosList }

    case IS_LOADING:
      return { ...state, isLoading: action.loading }

    default:
      return state;
  }

}

export const getTodoListsSuccess = (todosList) => ({ type: SET_TODOLISTS, todosList: todosList });
export const setTodoSuccess = (title) => ({ type: ADD_TODO, title: title });
const setLoading = (loading) => ({ type: IS_LOADING, loading })


export const setTodoLists = () => async (dispatch) => {
  dispatch(setLoading(false))
  let response = await todosApi.getTodos()
  dispatch(getTodoListsSuccess(response))
  dispatch(setLoading(true))
}

export const setTodo = (title) => async (dispatch) => {
  let response = await todosApi.addTodo(title)
  dispatch(setTodoSuccess(title))
  dispatch(setTodoLists())
}

export const delTodo = (todoId) => async (dispatch) => {
  let response = await todosApi.delTodo(todoId)
  dispatch(setTodoLists())
}



export default todosReducer;
