import React, { useEffect, useState } from 'react';
import styles from './Users.module.css'
import { connect } from 'react-redux';
import { setUsers } from '../../Redux/reducers/usersReducer';
import { setProfile, closeProfile } from '../../Redux/reducers/profileReducer';
import User from './User/User';
import Profile from '../Profile/Profile';
import Filter from '../common/Filter/Filter';
import { getUsers, getLoading, getProfile, getUsersLoading } from '../../Redux/selectors/usersSelectors';
import Preloader from '../common/Preloader/Preloader';
import SimpleModal from '../NewUserForm/Modal/Modal';
import { Button, Container } from '@material-ui/core';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import { makeStyles } from "@material-ui/core/styles";
import { Route, NavLink } from 'react-router-dom'

const useStyles = makeStyles(theme => ({
    root: {
        padding: "15px",
        backgroundColor: theme.palette.pageBackground,
        [theme.breakpoints.up('md')]: {
            padding: "60px"
        }
    },

}));
const Users = (props) => {
    const [filterValue, setFilterValue] = useState('');
    const [selectedId, setSelectedId] = useState(null);

    useEffect(() => {
        props.setUsers()
    }, []);

    const filterName = (value) => setFilterValue(value)

    const setUserId = (userId) => {
        props.setProfile(userId)
        setSelectedId(userId)
    }

    const closeProfile = () => {
        props.closeProfile();
        setSelectedId(null)
    }
    const classes = useStyles();
    return (
        <Container maxWidth="xl" className={classes.root} >
            <div className={styles.filterBox}>
                <Filter filterName={filterName} />
            </div>
            <div className={styles.addUserBox}>
                {/* <Route path={'/create/:id'} render={() => <SimpleModal /> } /> */}
                <Route path={'/create'} render={() => <SimpleModal />} />
                <NavLink to={'/create'} >
                    <Button variant="contained" color="primary" startIcon={<PersonAddIcon />} type="button">
                        Add new person
           </Button>
                </NavLink>
            </div>
            <div className={styles.users}>
                <div className={styles.usersBox}>
                    {!props.isUsersLoading ? <Preloader />
                        : props.usersList && props.usersList.filter((user) => user.username.toLocaleUpperCase().indexOf(filterValue.toLocaleUpperCase()) > -1).
                            map(user => <User key={user._id} user={user} selectedId={selectedId} setProfile={setUserId} profile={props.profile} isLoading={props.isLoading} />)}
                </div>
                <div className={styles.profileBox}>
                    {!props.isLoading && <div className={styles.loading}>Loading...</div>}
                    {props.profile
                        ? <Profile {...props.profile} closeProfile={closeProfile} />
                        : <div></div>
                    }
                </div>

            </div>
        </Container>
    )
}
const mapStateToProps = (state) => ({

    usersList: getUsers(state),
    isLoading: getLoading(state),
    profile: getProfile(state),
    isUsersLoading: getUsersLoading(state)
})

const mapDispatchToProps = (dispatch) => (
    {
        setUsers: () => dispatch(setUsers()),
        setProfile: (id) => dispatch(setProfile(id)),
        closeProfile: () => dispatch(closeProfile())

    }
)


export default connect(mapStateToProps, mapDispatchToProps)(Users);
