import React from 'react';
import styles from './User.module.css';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

export const User = (props) => {
    let elevation = props.selectedId === props.user._id ? 5 : 1;
    const viewProfile = (id) => props.setProfile(id)


    return (
        <Paper elevation={elevation}>
            <div className={styles.userBox}>
                <Typography variant="body1" component="span" >
                    {props.user.username}
                </Typography>
                <Button size="small" variant="outlined" color="primary" onClick={() => viewProfile(props.user._id)} disabled={!props.isLoading}>View profile</Button>
            </div>

        </Paper>



    )
}


export default User;


