import React from 'react';
import ContactMeForm from './ContactForm/ContactForm';
import { reset } from 'redux-form';
import { connect } from 'react-redux';
import { sendMessage } from '../../Redux/reducers/contactMeReduser';
import Preloader from '../common/Preloader/Preloader';
import { Container, Typography, Paper } from '@material-ui/core';
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(theme => ({
  root: {
    padding: "15px",
    backgroundColor: theme.palette.pageBackground,
    [theme.breakpoints.up('md')]: {
      padding: "60px"
    }

  },
  messageSent: {
    padding: "20px",
    backgroundColor: theme.palette.yellowColor
  }

}));


function Contacts(props) {
  const classes = useStyles();

  const sendMessageData = (value, dispatch) => {
    props.sendMessage(value)
    dispatch(reset('contact'))
  }
  return (
    <Container maxWidth="xl" className={classes.root}>
      {props.isMessageSend && <Preloader />}
      <Typography variant="h4" gutterBottom align='center'>Contact Me</Typography>
      {props.message
        ? <Container maxWidth="xs" className={classes.root}>
          <Paper elevation={3} className={classes.messageSent}>
            <Typography variant="h6" gutterBottom align='center'>Message sent!</Typography>
          </Paper>
        </Container>
        : <Container maxWidth="sm" >
          <ContactMeForm onSubmit={sendMessageData} />
        </Container>

      }

    </Container>
  );
}

const mapStateToProps = (state) => ({
  message: state.contactMe.message,
  isMessageSend: state.contactMe.isMessageSend
})

const mapDispatchToProps = (dispatch) => {
  return {
    sendMessage: (message) => dispatch(sendMessage(message))
  }

}


export default connect(mapStateToProps, mapDispatchToProps)(Contacts);