import React, { useState, useEffect } from 'react';
import styles from './Task.module.css';
import cn from 'classnames';
import BackspaceIcon from '@material-ui/icons/Backspace';
import {Typography, FormControlLabel, Select, Checkbox } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
    
  }));

const Task = (props) => {
    const { title, priority, completed, _id } = props
    const task = props.task
    const [isPrioritiEdit, setPriority] = useState(false)
    const [isCompleted, setCompleted] = useState(false)
    const classes = useStyles();

    const completedChange = (e) => {
        setCompleted(e.currentTarget.checked)
        let newTask = { ...task, completed: e.currentTarget.checked }
        props.changeTask(newTask)
    }

    const changePriority = (e) => {
        let newTask = { ...task, priority: e.currentTarget.value }
        props.changeTask(newTask)
    }

    const priorityEdit = () => {
        !completed&&setPriority(true)
    }
    const closePriorityEdit = () => setPriority(false)
    const delTask = () => props.delTask(_id)


    return (
        <div className={cn(styles.task, { [styles.completedTask]: completed })}>
            <Typography className={styles.title} variant="subtitle2">{title}</Typography>
            <div className={styles.completed}>
                <FormControlLabel
                    value="Completed:"
                    control={<Checkbox color="primary" onChange={completedChange} checked={completed} size="small" />}
                    label="Completed:"
                    labelPlacement="start"
                />
            </div>
            <div className={styles.priority} onDoubleClick={priorityEdit} onBlur={closePriorityEdit}><span>Priority:  </span>
                {isPrioritiEdit
                    ? <Select native onBlur={changePriority}>
                        <option>Low</option>
                        <option>Medium</option>
                        <option>High</option>
                    </Select>
                    : <span className={cn(styles.priorityColor, {
                        [styles.low]: priority === 'Low',
                        [styles.medium]: priority === 'Medium',
                        [styles.high]: priority === 'High',
                    })} >{priority}</span>
                }
            </div>
            <BackspaceIcon className={styles.del} onClick={delTask}  fontSize="small"/>
        </div>
    )
}

export default Task;
