import React from 'react';
import { reset } from 'redux-form';
import styles from './NewUserForm.module.css';
import { AddNewUserForm } from './UserForm/UserForm'
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import { setNewUser } from '../../Redux/reducers/addNewUserFormReducer'



const NewUserForm = (props) => {
    const sendUserData = (value, dispatch) => {
        props.setNewUser(value)
        props.handleClose()
        dispatch(reset('UserForm'))
    }
    return (
        <div className= {styles.newUserForm}>
            <Typography variant="h6" gutterBottom color='primary'>
                Add new person.
             </Typography>
            <AddNewUserForm onSubmit={sendUserData} />
        </div>
    )
}

const mapDispatchToProps = (dispatch) => ({
    setNewUser: (value) => dispatch(setNewUser(value)),
})



export default connect(null, mapDispatchToProps)(NewUserForm);