import { contactApi } from "../../api/contactMeApi";


const SET_MESSAGE ='contactMeReducer/SET_MESSAGE'
const MESSAGE_SENT ='contactMeReducer/MESSAGE_SENT'
 
const initialState = {
    message:null,
    isMessageSend: false
}
 const contactMeReduser = (state = initialState, action) => {

    switch (action.type) {
        case SET_MESSAGE:
          return {
            ...state,
            message : action.message
          }
          case MESSAGE_SENT : return{
            ...state,
            isMessageSend: action.isMessageSend
          }
        default:
            return state;
    }
    
}

const messageSuccess = (message)=>({type: SET_MESSAGE, message});
const messageSendingSuccess = (isMessageSend)=>({type:MESSAGE_SENT,isMessageSend })

 export const sendMessage = (message)=> async (dispatch)=>{
   dispatch(messageSendingSuccess(true))
    let response = await contactApi.sendMeMessage(message)
    dispatch(messageSuccess(response))
    dispatch(messageSendingSuccess(false))
}

export default contactMeReduser;
