import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { renderTextField } from '../../common/FormControls/FormControls'
import { Box, Button } from '@material-ui/core';
import {validate} from '../../../utils/validation/validationForm'



const ContactForm = (props) => {
  const { submitting,pristine, handleSubmit } = props;
  return (
    <form onSubmit={handleSubmit} noValidate>
      <Box m={2}>
        <Field name="name" component={renderTextField} label="Name" variant="outlined" fullWidth />
      </Box>
      <Box m={2}>
        <Field name="email" component={renderTextField} label="Email" variant="outlined" fullWidth />
      </Box>
      <Box m={2}>
        <Field name="text" component={renderTextField} label="Message" variant="outlined" fullWidth multiline rows="4" />
      </Box>
      <Box m={2} align='center'>
        <Button  variant="contained" color="primary" type="submit" disabled={pristine||submitting}>Send</Button>
      </Box>
    </form>
  );
}
const ContactMeForm = reduxForm({ form: 'contact', validate })(ContactForm)

export default ContactMeForm;
