import React from 'react'
import styles from './UserForm.module.css';
import { Field, reduxForm } from 'redux-form'
import { validate } from '../../../utils/validation/validationForm'
import { renderTextField } from '../../common/FormControls/FormControls'

import Button from '@material-ui/core/Button';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    root: {
      '& .MuiTextField-root': {
        margin: theme.spacing(1),
        width: 250,
        [theme.breakpoints.up('sm')]: {
            width: 350,
          }
      }
    },
  }));


const UserForm = props => {
    const classes = useStyles();
    const { handleSubmit, pristine, reset, submitting} = props
    return (
        <form onSubmit={handleSubmit} className={classes.root} noValidate autoComplete="off">
             <div>
                <Field name="username" component={renderTextField} label="User name"/>
            </div>
            <div>
                <Field name="firstName" component={renderTextField} label="First Name"/>
            </div>
            <div>
                <Field name="lastName" component={renderTextField} label="Last Name" />
            </div>
            <div>
                <Field name="email" component={renderTextField} label="Email" />
            </div>
            <div>
                <Field name="address" component={renderTextField} label="Address" />
            </div>
            <div>
                <Field name="city" component={renderTextField} label="City" />
            </div>
            <div>
                <Field name="zipcode" component={renderTextField} label="Zipcode" />
            </div>
            <div className={styles.btnBox}>
                <Button size="small" variant="contained" color="primary" type="submit" disabled={pristine || submitting}>Add</Button>
                <Button  size="small" variant="contained" type="button" disabled={pristine || submitting} onClick={reset}> Clear</Button>
            </div>
        </form>
    )
}

export const AddNewUserForm = reduxForm({
    form: 'UserForm',
    validate
})(UserForm)




