import React from 'react';
import styles from './Todo.module.css'
import AddBlock from '../../common/AddBlock/AddBlock';
import Task from '../Task/Task';
import {Paper, Box} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';


const Todo = (props) => {
  
    const { title, _id, taskList } = props
    const addTask = (title) => props.addTask(title, _id)
    return (
        <Paper  elevation={3} className={styles.todo}> 
        <DeleteIcon className={styles.del} onClick={() => props.delTodo(_id)} />
            {/* <span className={styles.del} onClick={() => props.delTodo(_id)}>X</span> */}
            <p className={styles.title}>{title}</p>
            <AddBlock addBlock={addTask} placeholder={'Add task...'}/>
            <Box variant="outlined" elevation={0}  p={1} bgcolor="background.paper">
                {taskList.length !== 0
                    ? taskList.map(task => <Task key={task._id} {...task} task = {task}
                        delTask={props.delTask} 
                        changeTask={props.changeTask}/>)
                    : <span>Add task</span>}
            </Box>
        </Paper>
        
    )
}

export default Todo;
